import React from 'react';
import { Container} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import {Button} from 'react-bootstrap';
class App extends React.Component{
    constructor(props){
      super(props);
      this.state={task:'',tasks:[]};
      this.tasks=[];
      this.handlechange=this.handlechange.bind(this);
      this.handlesubmit=this.handlesubmit.bind(this);
      }
      handlechange(e){
        this.setState({task:e.target.value});
      }
      handlesubmit(e){
        const after_list=this.state.tasks;
        after_list.push(this.state.task);
        this.setState({
            tasks: after_list,
            task: ''
        });
       // console.log(this.tasks);
      }
      delete(id){
        const after_list=this.state.tasks;
        after_list.splice(id,1);
        this.setState({
            tasks:after_list
        });
    }
    render(){
      return(
        <div>
      <Container className="App">
            <h1>
            TODO
            </h1>
            <h5><Button variant="danger" style={{"float":"right"}}  onClick={() => { window.location.href='/react_todo';}}>Exit</Button></h5>
          <form>
          <h4>Got any Todo?
          </h4>
          <h5><input type="text" value={this.state.task} onChange={this.handlechange} style={{"height": "35px"}}/>
          &nbsp;
          <Button variant="dark" className="add" type="button"  onClick={this.handlesubmit}><b>#Add to TODO</b></Button></h5>   
          <h4>Todo-List:</h4>
          <TodoList items={this.state.tasks} _handleDelete={this.delete.bind(this)}/>         
          </form>
       </Container>
      </div>
      )
    }
  }
class TodoList extends React.Component {

_handleDelete(id){
this.props._handleDelete(id);
}

render() {
return (
   <ul>
       {this.props.items.map((item,index)=> (
           <li key={index}><b>{item}</b><Button variant="success" size="sm" className="delete" type="button" style={{"float":"right"}} onClick={this._handleDelete.bind(this,index)}><b>Completed</b></Button></li>
       ))}
   </ul>
);
}
}

  export default App;